
*{margin:0;padding:0;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  overflow:hidden;
}
h1, h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
}
a {
  text-decoration: none;
}
h1{font-size:18px;padding:10px 0;margin:0 0 10px;position:relative;
}
h1:after{content:"";width:100px;height:2px;background:#147;position:absolute;bottom:0;left:50%;margin-left:-50px;
}
.menu{display:block;width:80%;margin:auto;overflow:hidden;
}
.menusub{display:block;height:40px;line-height:40px;text-align:center;background:#666;border:solid 1px #ccc;border-radius:10px;margin:10px;
}
.menua{font-size:16px;color:#fff;
}
.nav{-webkit-box-shadow:0 2px 10px 2px rgba(0,0,0,0.05);box-shadow:0 2px 10px 2px rgba(0,0,0,0.05);
}
.navtext{text-align:center;font-size:20px;height:60px;line-height:60px;background:#fff;position:relative;z-index:999;
}
.navicon,.close{width:20px;height:30px;position:absolute;top:0;right:10px;margin-top:15px;z-index:999;overflow:hidden;
}
.navicon span,.close span{display:block;width:20px;height:2px;position:absolute;top:50%;left:0;-webkit-transform-origin:center center;transform-origin:center center;border-radius:2px;
}
.close span{background:#000;
}
.navicon span{background:#000;
}
.navicon span:nth-child(1){margin-top:-8px;
}
.navicon span:nth-child(2){margin-top:-1px;
}
.navicon span:nth-child(3){margin-top:6px;
}
.close span:nth-child(1){margin-top:-1px;-webkit-transform:rotate(45deg);transform:rotate(45deg);
}
.close span:nth-child(2){display:none;
}
.close span:nth-child(3){margin-top:-1px;-webkit-transform:rotate(-45deg);transform:rotate(-45deg);
}
.mask{width:100%;height:100%;position:fixed;top:0;left:0;z-index:99;background:rgba(0,0,0,0.8);
}
.nav_list{position:absolute;top:60px;left:0;right:0;z-index:9999;display:-webkit-box;display:-ms-flexbox;display:flex;-webkit-box-orient:horizontal;-webkit-box-direction:normal;-ms-flex-flow:row wrap;flex-flow:row wrap;padding:4px;
}
.nav_list li{width:33.33%;position:relative;overflow:hidden;
}
.nav_list li a{display:block;height:36px;line-height:36px;font-size:14px;color:#fff;border:solid 1px rgba(255,255,255,0.2);background:rgba(255,255,255,0.1);margin:2px;border-radius:20px;overflow:hidden;
}


































































